import { Component } from '@angular/core';


interface AngSelectEvent {
    name: string;
    value: any;
}

@Component({
    selector: 'select-events',
    template: `
        <div id="s1"></div>
        <label>Open, close, focus, blur, change events</label>
        <pre class="hljs" ngNonBindable><code class="lang-html"><span class="hljs-tag">&lt;<span class="hljs-name">ng-select</span> <span class="hljs-attr">snippet</span>=<span class="hljs-string">"s1"</span>
           [<span class="hljs-attr">items</span>]=<span class="hljs-string">"cities"</span>
           [(<span class="hljs-attr">ngModel</span>)]=<span class="hljs-string">"selectedCity"</span>
           <span class="hljs-attr">bindLabel</span>=<span class="hljs-string">"name"</span>
           <span class="hljs-attr">bindValue</span>=<span class="hljs-string">"id"</span>
           (<span class="hljs-attr">open</span>)=<span class="hljs-string">"onOpen()"</span>
           (<span class="hljs-attr">close</span>)=<span class="hljs-string">"onClose()"</span>
           (<span class="hljs-attr">focus</span>)=<span class="hljs-string">"onFocus($event)"</span>
           (<span class="hljs-attr">blur</span>)=<span class="hljs-string">"onBlur($event)"</span>
           (<span class="hljs-attr">change</span>)=<span class="hljs-string">"onChange($event)"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">ng-select</span>&gt;</span></code></pre>

<ng-select snippet="s1"
           [items]="cities"
           [(ngModel)]="selectedCity"
           bindLabel="name"
           bindValue="id"
           (open)="onOpen()"
           (close)="onClose()"
           (focus)="onFocus($event)"
           (blur)="onBlur($event)"
           (change)="onChange($event)">
</ng-select>

        <div *ngIf="events.length > 0">
            <br>
            <button (click)="events = []" class="btn btn-secondary btn-sm">Clear events</button>
            <br>
            <br>
        </div>

        <div *ngFor="let event of events">
            <small>{{event.name}} - {{event.value | json}}</small>
            <hr>
        </div>
    `
})
export class SelectEventsComponent {

    selectedCity: any;
    cities = [
        {id: 1, name: 'Vilnius'},
        {id: 2, name: 'Kaunas'},
        {id: 3, name: 'Pavilnys', disabled: true}
    ];

    events: AngSelectEvent[] = [];

    onChange($event) {
        this.events.push({name: '(change)', value: $event});
    }

    onFocus($event: Event) {
        this.events.push({name: '(focus)', value: $event});
    }

    onBlur($event: Event) {
        this.events.push({name: '(blur)', value: $event});
    }

    onOpen() {
        this.events.push({name: '(open)', value: null});
    }

    onClose() {
        this.events.push({name: '(close)', value: null});
    }
}





