import { Component, EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';

@Component({
    selector: 'select-tags',
    template: `
        <label>Default tags</label>
        <pre class="hljs" ngNonBindable><code class="lang-html"><span class="hljs-tag">&lt;<span class="hljs-name">ng-select</span> [<span class="hljs-attr">items</span>]=<span class="hljs-string">"companies"</span>
        [<span class="hljs-attr">addTag</span>]=<span class="hljs-string">"true"</span>
        <span class="hljs-attr">bindLabel</span>=<span class="hljs-string">"name"</span>
        [(<span class="hljs-attr">ngModel</span>)]=<span class="hljs-string">"selectedCompany"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">ng-select</span>&gt;</span></code></pre>

<ng-select [items]="companies"
        [addTag]="true"
        bindLabel="name"
        [(ngModel)]="selectedCompany">
</ng-select>
        <p>
            Selected value: {{selectedCompany | json}}
        </p>
        <hr>

        <label>Custom tags</label>
        <pre class="hljs" ngNonBindable><code class="lang-html"><span class="hljs-tag">&lt;<span class="hljs-name">ng-select</span> [<span class="hljs-attr">items</span>]=<span class="hljs-string">"companies"</span>
        [<span class="hljs-attr">addTag</span>]=<span class="hljs-string">"addTag"</span>
        <span class="hljs-attr">multiple</span>=<span class="hljs-string">"true"</span>
        <span class="hljs-attr">bindLabel</span>=<span class="hljs-string">"name"</span>
        [(<span class="hljs-attr">ngModel</span>)]=<span class="hljs-string">"selectedCompanyCustom"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">ng-select</span>&gt;</span></code></pre>

<ng-select [items]="companies"
        [addTag]="addTag"
        multiple="true"
        bindLabel="name"
        [(ngModel)]="selectedCompanyCustom">
</ng-select>
        <p>
            Selected value: {{selectedCompanyCustom | json}}
        </p>
        <hr>
    `
})
export class SelectTagsComponent {

    companies: any[] = [];

    companiesNames = ['Miškas', 'Žalias', 'Flexigen'];

    ngOnInit() {
        this.companiesNames.forEach((c, i) => {
            this.companies.push({ id: i, name: c });
        });
    }

    addTag = (name) => {
        return { name: name, tag: true };
    }
}





