import {Component} from '@angular/core';

@Component({
    selector: 'select-bindings',
    template: `
        <label>Bind to default <b>label</b>, <b>object</b> bindings</label>
        <pre class="hljs" ngNonBindable><code class="lang-html"><span class="hljs-tag">&lt;<span class="hljs-name">ng-select</span> [<span class="hljs-attr">items</span>]=<span class="hljs-string">"defaultBindingsList"</span>
           [(<span class="hljs-attr">ngModel</span>)]=<span class="hljs-string">"selectedCity2"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">ng-select</span>&gt;</span></code></pre>

<ng-select [items]="defaultBindingsList"
           [(ngModel)]="selectedCity2">
</ng-select>
        <p>
            Selected city object: {{selectedCity2 | json}}
        </p>
        <hr>
        <label>Bind label to custom property</label>
        <pre class="hljs" ngNonBindable><code class="lang-html"><span class="hljs-tag">&lt;<span class="hljs-name">ng-select</span> [<span class="hljs-attr">items</span>]=<span class="hljs-string">"cities"</span>
           <span class="hljs-attr">bindLabel</span>=<span class="hljs-string">"name"</span>
           <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Select value"</span>
           [<span class="hljs-attr">clearable</span>]=<span class="hljs-string">"false"</span>
           [(<span class="hljs-attr">ngModel</span>)]=<span class="hljs-string">"selectedCity"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">ng-select</span>&gt;</span></code></pre>

<ng-select [items]="cities"
           bindLabel="name"
           placeholder="Select value"
           [clearable]="false"
           [(ngModel)]="selectedCity">
</ng-select>
        <p>
            Selected city object: {{selectedCity | json}}
        </p>
        <hr>
        <label>Bind label and model to custom properties</label>
        <pre class="hljs" ngNonBindable><code class="lang-html"><span class="hljs-tag">&lt;<span class="hljs-name">ng-select</span> [<span class="hljs-attr">items</span>]=<span class="hljs-string">"cities"</span>
           <span class="hljs-attr">bindLabel</span>=<span class="hljs-string">"name"</span>
           <span class="hljs-attr">bindValue</span>=<span class="hljs-string">"id"</span>
           [(<span class="hljs-attr">ngModel</span>)]=<span class="hljs-string">"selectedCityId2"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">ng-select</span>&gt;</span></code></pre>

<ng-select [items]="cities"
           bindLabel="name"
           bindValue="id"
           [(ngModel)]="selectedCityId2">
</ng-select>
        <p>
            Selected city ID: {{selectedCityId2 | json}}
        </p>
    `
})
export class SelectBindingsComponent {

    defaultBindingsList = [
        {value: 1, label: 'Vilnius'},
        {value: 2, label: 'Kaunas'},
        {value: 3, label: 'Pavilnys', disabled: true}
    ];

    cities = [
        {id: 1, name: 'Vilnius'},
        {id: 2, name: 'Kaunas'},
        {id: 3, name: 'Pavilnys', disabled: true}
    ];

    selectedCity: any;
    selectedCity2: number = null;
    selectedCityId2: number = null;

    ngOnInit() {
    }
}




